import BigNumber from 'bignumber.js'
import { Fragment } from 'react'
import { AppBar, Button, Link, Tab, Tabs } from '@material-ui/core'
import { makeStyles } from '@material-ui/styles'
import LockIcon from '@material-ui/icons/Lock'
import { FormatTooltip } from './index'

export default function BaseModule(props) {
  const s = props.size === 'small'
  const classes = makeStyles(theme => ({
    inputModule: {
      backgroundColor: '#F5FAFF',
      borderRadius: '25px',
      color: 'black',
      marginTop: '44px',
      padding: '10px'
    },
    metaModule: {
      backgroundColor: 'rgba(238 238 238 / 85%)',
      borderRadius: '25px',
      boxShadow: 'none',
      color: 'black',
      marginTop: '16px',
      padding: '10px 16px 30px 16px'
    },
    sectionTab: {
      fontFamily: 'Futura-Pt-Book',
      fontSize: s ? '14px' : '18px',
      minWidth: '44px',
      textTransform: 'uppercase',
      borderRadius: '15px'
    },
    formButton: {
      borderRadius: '15px',
      fontFamily: 'Futura-Pt-Book',
      fontSize: 'calc(12px + 1vmin)',
      height: '44px',
      margin: '12px 12px',
      width: '64%',
      maxWidth: '200px'
    },
    indicator: {
      backgroundColor: 'transparent',
      display: 'flex',
      justifyContent: 'center',
      '& > span': {
        backgroundColor: '#627264',
        borderRadius: '3.5px',
        height: '7px',
        marginTop: '-8px',
        maxWidth: '7px',
        width: '100%'
      }
    },
    noIndicator: {
      backgroundColor: 'transparent'
    }
  }))()

  function a11yProps(index) {
    return {
      key: `${index}`,
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`
    }
  }

  const allowanceCallback = (transactionState) => {
    switch(transactionState) {
      case 1:
        props.setAllowance(new BigNumber(-1))
        break
      case 2:
        props.setAllowance(new BigNumber(1))
        break
      case 3:
        if (props.allowance.isEqualTo(-1)) {
          props.setAllowance(0)
        }
        break
    }
  }

  const approveHandler = () => {
    props.handleApprove(allowanceCallback)
  }

  let buttonLabel
  let buttonHandler
  if (props.allowance.isEqualTo(0)) {
    buttonLabel = 'APPROVE'
    buttonHandler = approveHandler
  } else if (props.locked) {
    buttonLabel = (
      <Fragment>
        <LockIcon/>
        <div style={{style: 'inline-block'}}>{`${props.lockedSeasons} seasons`}</div>
      </Fragment>
    )
  } else if (props.allowance.isGreaterThan(0)) {
    buttonLabel = props.sectionTitles[props.section]
    buttonHandler = props.handleForm
  } else {
    buttonLabel = 'WAITING . . .'
    buttonHandler = null
  }

  let showButton
  if (props.showButton) {
    showButton = (
      <FormatTooltip
        placement='top'
        margin='0 0 0 7px'
        title={props.locked ? 'Unvote Active BIPs to Withdraw' : ''}
      >
        <div>
          <Button
            className={classes.formButton}
            color='primary'
            disabled={buttonLabel !== 'APPROVE' && (buttonHandler === null || props.isDisabled || props.locked)}
            onClick={buttonHandler}
            variant='contained'
          >
            {buttonLabel}
          </Button>
        </div>
      </FormatTooltip>
    )
  } else {
    <></>
  }

  const moduleContent = (
    <>
    <div style={{position: 'relative', zIndex: '0'}}>
      {props.children}
      {props.allowance.isEqualTo(0)
        ? <div style={{backgroundColor: '#F5FAFF', opacity: '50%', position: 'absolute', top: '0', left: '0', width: '100%', height: '100%', zIndex: '1'}}>
          </div>
        : null
      }
    </div>
    {showButton}
    {props.allowance.isEqualTo(0)
      ? <span>
          To use this module, send an Approval by clicking the Approve button above.
          <br/>
          <Link style={{color: 'green'}} href='' onClick={(event) => {
            event.preventDefault()
            props.resetForm()
          }}>
            Reset Defaults
          </Link>
        </span>
      : null
    }
    </>
  )

  return (
    <AppBar style={props.style} className={props.removeBackground ? classes.metaModule : classes.inputModule} position='static'>
      <Tabs
        classes={props.sectionTitles.length > 1 ? {indicator: classes.indicator} : {indicator: classes.noIndicator}}
        indicatorColor='primary'
        onChange={props.handleTabChange}
        style={{width: '100%', borderRadius: '15px'}}
        TabIndicatorProps={{ children: <span /> }}
        textColor='primary'
        value={props.section}
        variant='fullWidth'
      >
      {
        props.sectionTitles.map((sectionTitle, index) => {
          return (
            <Tab
              className={classes.sectionTab}
              disabled={props.sectionTitles.length < 2}
              label={sectionTitle}
              style={{color: 'black'}}
              {...a11yProps(index)}
            />
          )
        })
      }
      </Tabs>
      <hr style={{color: 'primary', backgroundColor: 'primary', margin: '4px 8px'}} />
      {props.showButton
        ? <form autoComplete='off' noValidate style={{padding: '0 10px'}}>
            {moduleContent}
          </form>
        : <span style={{padding: '0px 10px'}}>
            {moduleContent}
          </span>
      }
    </AppBar>
  )
}

BaseModule.defaultProps = {
  allowance: new BigNumber(1),
  isDisabled: false,
  locked: false,
  showButton: true,
  removeBackground: false,
}
