import React, { useState, useEffect } from 'react'
import { BaseModule, ContentSection } from '../Common'
import { Chart } from './Chart'
import { hourUniswapQuery, dayUniswapQuery, dayBeanQuery, hourBeanQuery } from '../../graph'


export default function Charts(props) {
  const [chartData, setChartData] = useState({
    price: [[],[]],
    liquidity: [[],[]],
    volume: [[],[]],
    marketCap: [[],[]]
  })
  const [section, setSection] = useState(0)
  const sectionTitles = ['Price', 'Volume', 'Liquidity', 'Market Cap']

  const [dataMode, setDataMode] = useState('hr')
  const [timeMode, setTimeMode] = useState('week')

  const [width, setWidth] = useState<number>(window.innerWidth);

  function handleWindowSizeChange() {
          setWidth(window.innerWidth);
  }

  let isMobile: boolean = (width <= 550);
  console.log(props.marginTop)
  const marginTop = props.marginTop == null ? '-80px' : props.marginTop

  useEffect(() => {
    loadUniswapCharts()
    window.addEventListener('resize', handleWindowSizeChange);
    return () => {
        window.removeEventListener('resize', handleWindowSizeChange);
    }
  }, [])

  const modeProps = {
    dataMode: dataMode,
    timeMode: timeMode,
    setDataMode: setDataMode,
    setTimeMode: setTimeMode
  }


  const sections = [
                    <Chart {...modeProps} size={isMobile ? 'small' : 'medium'} key='price' {...props} title='Price' data={chartData.price}/>,
                    <Chart {...modeProps} size={isMobile ? 'small' : 'medium'} key='volume' {...props} title='Volume' data={chartData.volume}/>,
                    <Chart {...modeProps} size={isMobile ? 'small' : 'medium'} key='liquidity' {...props} title='Liquidity' data={chartData.liquidity}/>,
                    <Chart {...modeProps} timeMode={timeMode} setDataMode={setDataMode} size={isMobile ? 'small' : 'medium'} key='marketCap' {...props} title='Market Cap' data={chartData.marketCap}/>
                   ]


  const baseStyle = isMobile ? {width:'100vw', paddingLeft:0, paddingRight:0} : null

  return (
    <ContentSection id='charts' title='Charts' size='20px' style={{maxWidth: '1000px', marginTop:marginTop}}>
      <BaseModule
        size={"small"}
        style={baseStyle}
        handleTabChange={(event, newSection) => { setSection(newSection) }}
        section={section}
        sectionTitles={sectionTitles}
        showButton={false}
        removeBackground={true}
      >
        {sections[section]}
      </BaseModule>
    </ContentSection>
  )

  async function loadUniswapCharts() {
    const [dayData, hourData, beanDayData, beanHourData] = await Promise.all([
      dayUniswapQuery(),
      hourUniswapQuery(),
      dayBeanQuery(),
      hourBeanQuery()
    ])
    const price = [beanHourData.map((d) => {return {x: d.x, y: d.price}}),
                  beanDayData.map((d) => {return {x: d.x, y: d.price}})]
    const volume = [hourData.map((d) => {return {x: d.x, y: d.volume}}),
                  dayData.map((d) => {return {x: d.x, y: d.volume}})]
    const liquidity = [hourData.map((d) => {return {x: d.x, y: d.liquidity}}),
                  dayData.map((d) => {return {x: d.x, y: d.liquidity}})]
    const marketCap = [beanHourData.map((d) => {return {x: d.x, y: d.totalSupplyUSD}}),
                  beanDayData.map((d) => {return {x: d.x, y: d.totalSupplyUSD}})]
    setChartData({price: price, volume: volume, liquidity: liquidity, marketCap: marketCap})

  }
  }
