import BigNumber from 'bignumber.js'
import {
  BEAN,
  UNI_V2_ETH_BEAN_LP,
  UNI_V2_USDC_ETH_LP
} from '../constants'
import {
  account,
  beanstalkContractReadOnly,
  pairContractReadOnly,
  tokenContractReadOnly,
  txCallback
} from './index'

const IGNORED_EVENTS = new Set(['SeasonSnapshot', 'WeatherChange', 'Swap', 'Transfer'])

const benchmarkStart = (operation) => {
  console.log(`LOADING ${operation}`)
  return Date.now()
}
const benchmarkEnd = (operation, startTime) => {
  console.log(`LOADED ${operation} (${(Date.now() - startTime) / 1e3} seconds)`)
}

let listeningForEvents = false
let lastPriceRefresh = (new Date()).getTime()
let lastTotalsRefresh = (new Date()).getTime()
let newEventHashes = new Set()

export async function initializeEventListener(callback, updatePrices, updateTotals, setContractEvents) {
  const startTime = benchmarkStart('EVENT LISTENER')

  const beanstalk = beanstalkContractReadOnly()
  const bean = tokenContractReadOnly(BEAN)
  const beanPair = pairContractReadOnly(UNI_V2_ETH_BEAN_LP)
  const usdcPair = pairContractReadOnly(UNI_V2_USDC_ETH_LP)

  const events = await beanstalk.getPastEvents('allEvents', {
    fromBlock: 0,
    toBlock: 'latest'
  })
  benchmarkEnd('EVENT LISTENER (BEANSTALK)', startTime)

  let allEvents = events.filter((event) => (event.returnValues.account === account))

  Promise.all([
    beanPair.getPastEvents('allEvents', {
      fromBlock: 0,
      toBlock: 'latest'
    }),
    bean.getPastEvents('allEvents', {
      fromBlock: 0,
      toBlock: 'latest'
    }),
  ]).then((contractEventLists) => {
    benchmarkEnd('EVENT LISTENER (BEAN/PAIR)', startTime)
    const filteredEvents = Array.prototype.concat.apply([], contractEventLists).filter((event) => (
      !IGNORED_EVENTS.has(event.event)
      && (event.returnValues.from === account
          || event.returnValues.to === account
          || event.returnValues.spender === account
      )
    ))
    allEvents = allEvents.concat(filteredEvents)
    setContractEvents(allEvents)
  })

  if (listeningForEvents) {
    benchmarkEnd('EVENT LISTENER (already listening)', startTime)
    return allEvents
  }
  listeningForEvents = true

  /* Listen for new Contract events */
  beanPair.events.allEvents({fromBlack: 'latest'}, function (error, event) {
    if ((new Date()).getTime() - lastPriceRefresh > 5000
        && (event.returnValues.to === undefined
            || event.returnValues.to.toLowerCase() !== account.toLowerCase())) {
      console.log('UPDATING PRICES!')
      updatePrices()
      lastPriceRefresh = (new Date()).getTime()
    }

    /* Temporarily Ignoring Bean Pair events
    if (IGNORED_EVENTS.has(event.event)) {
      return
    }
    const newEventHash = event.transactionHash + String(event.logIndex)
    if (newEventHashes.has(newEventHash)) {
      return
    }
    newEventHashes.add(newEventHash)

    if (error === null
        && (event.returnValues.from === account
            || event.returnValues.to === account
            || event.returnValues.spender === account)) {
      allEvents = [...allEvents, event]
      setContractEvents(allEvents)
    }
    else if (event.returnValues.to === UNISWAP_V2_ROUTER) {
      web3.eth.getTransaction(event.transactionHash).then((t) => {
        if (t.from === account) {
          allEvents = [...allEvents, event]
          setContractEvents(allEvents)
        }
      })
    }
    */
  })
  usdcPair.events.Swap({fromBlack: 'latest'}, function (error, event) {
    if ((new Date()).getTime() - lastPriceRefresh > 5000) {
      console.log('UPDATING PRICES!')
      updatePrices()
      lastPriceRefresh = (new Date()).getTime()
    }
  })
  beanstalk.events.allEvents({fromBlock:'latest'}, function (error, event) {
    if (IGNORED_EVENTS.has(event.event)) {
      return
    }
    const newEventHash = event.transactionHash + String(event.logIndex)
    if (newEventHashes.has(newEventHash)) {
      return
    }
    newEventHashes.add(newEventHash)

    if (event.returnValues.account !== undefined
        && event.returnValues.account.toLowerCase() === account.toLowerCase()) {
      allEvents = [...allEvents, event]
      callback(allEvents)
    } else if (event.event === 'Sunrise') {
      callback(allEvents)
      txCallback()
      lastTotalsRefresh = (new Date()).getTime()
      lastPriceRefresh = (new Date()).getTime()
    } else {
      if ((new Date()).getTime() - lastTotalsRefresh > 5000) {
        console.log('UPDATING TOTALS!')
        updateTotals()
        lastTotalsRefresh = (new Date()).getTime()
      }
    }
  })

  benchmarkEnd('EVENT LISTENER', startTime)
  return allEvents
}

export function parseWithdrawals(withdrawals, index: BigNumber) {
   let receivable = new BigNumber(0)
   let transit = new BigNumber(0)
   let transitWithdrawals = {}
   let receivableWithdrawals = {}
   Object.keys(withdrawals).forEach(s => {
     if ((new BigNumber(s)).isLessThanOrEqualTo(index)) {
       receivable = receivable.plus(withdrawals[s])
       receivableWithdrawals[s] = withdrawals[s]
     }
     else {
       transit = transit.plus(withdrawals[s])
       transitWithdrawals[s] = withdrawals[s]
     }
   })
   return [transit, receivable, transitWithdrawals, receivableWithdrawals]
}

export function addRewardedCrates(crates, season, rewardedBeans, rewardedStalk) {
  if (rewardedBeans.isEqualTo(0)) return crates
  let rewardedSeasons = (
    rewardedStalk
      .dividedBy(rewardedBeans.multipliedBy(0.0002))
      .integerValue(BigNumber.ROUND_DOWN)
  )
  rewardedStalk = (
    rewardedStalk
      .minus(rewardedSeasons.multipliedBy(rewardedBeans).multipliedBy(0.0002))
  )
  // BigNumber.set({ ROUNDING_MODE: BigNumber.ROUND_DOWN})
  // const previousSeasonBeans = new BigNumber(rewardedStalk.dividedBy(0.0002).toFixed(6))
  // const seasonBeans = new BigNumber(rewardedBeans.minus(previousSeasonBeans).toFixed(6))
  BigNumber.set({ ROUNDING_MODE: BigNumber.ROUND_HALF_UP})
  const ds = parseInt(season.minus(rewardedSeasons))
  crates[ds] = crates[ds] !== undefined ? crates[ds].plus(rewardedBeans) : rewardedBeans
  // crates[ds] = (crates[ds] === undefined) ? seasonBeans : crates[ds].plus(seasonBeans)
  // if (previousSeasonBeans.isGreaterThan(0)) {
  //   const pds = parseInt(season.minus(rewardedSeasons).minus(1))
  //   crates[pds] = (crates[pds] === undefined) ? previousSeasonBeans : crates[pds].plus(previousSeasonBeans)
  // }
  return crates
}

export function parsePlots(plots, index: BigNumber) {
   let pods = new BigNumber(0)
   let harvestablePods = new BigNumber(0)
   let unharvestablePlots = {}
   let harvestablePlots = {}
   Object.keys(plots).forEach(p => {
     if ((plots[p].plus(p)).isLessThanOrEqualTo(index)) {
       harvestablePods = harvestablePods.plus(plots[p])
       harvestablePlots[p] = plots[p]
     }
     else if ((new BigNumber(p)).isLessThan(index)) {
       harvestablePods = harvestablePods.plus(index.minus(p))
       pods = pods.plus(plots[p].minus(index.minus(p)))
       harvestablePlots[p] = index.minus(p)
       unharvestablePlots[index.minus(p).plus(p)] = plots[p].minus(index.minus(p))
     }
     else {
       pods = pods.plus(plots[p])
       unharvestablePlots[p] = plots[p]
     }
   })

   return [pods, harvestablePods, unharvestablePlots, harvestablePlots]
}
