import BigNumber from 'bignumber.js'
import React, { useEffect, useState } from 'react'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import {
  BEAN,
  BASE_SLIPPAGE,
  ETH,
  SLIPPAGE_THRESHOLD
} from '../../constants'
import {
  approveBeanstalkBean,
  buyAndSowBeans,
  displayBN,
  getFromAmount,
  getToAmount,
  harvest,
  MaxBN,
  MinBNs,
  sowBeans,
  SwapMode,
  toBaseUnitBN,
  toStringBaseUnitBN,
  TrimBN
} from '../../util'
import {
  BaseModule,
  CryptoAsset,
  EthInputField,
  FarmAsset,
  FrontrunText,
  InputFieldPlus,
  ListTable,
  SettingsFormModule,
  TokenInputField,
  TokenOutputField
} from '../Common'

export default function SowHarvestModule(props) {
  const [section, setSection] = useState(0)
  const [sectionInfo, setSectionInfo] = useState(0)
  const [page, setPage] = React.useState(0)

  const [beanValue, setBeanValue] = useState(new BigNumber(-1))
  const [podValue, setPodValue] = useState(new BigNumber(0))
  const [fromValue, setFromValue] = section === 0 ? [beanValue, setBeanValue] : [podValue, setPodValue]
  const [fromEthValue, setFromEthValue] = useState(new BigNumber(-1))
  const [buyBeanValue, setBuyBeanValue] = useState(new BigNumber(0))
  const [toValue, setToValue] = section !== 0 ? [beanValue, setBeanValue] : [podValue, setPodValue]
  const [harvestPlots, setHarvestPlots] = useState([])
  const fromToken = section === 0 ? CryptoAsset.Bean : FarmAsset.Pods
  const toToken = section !== 0 ? CryptoAsset.Bean : FarmAsset.Pods
  const [settings, setSettings] = useState({
    claim: false,
    mode: null,
    slippage: new BigNumber(BASE_SLIPPAGE),
  })

  let sectionTitles = ['Sow']
  if (props.harvestablePodBalance.isGreaterThan(0)) sectionTitles.push('Harvest')

  const handleTabChange = (event, newSection) => {
    setSection(newSection)
    setSettings({...settings, claim:false})
    if (newSection === 1) {
      setBeanValue(props.harvestablePodBalance)
      setPodValue(props.harvestablePodBalance)
      setHarvestPlots(Object.keys(props.harvestablePlots).map(key => (toStringBaseUnitBN(new BigNumber(key), BEAN.decimals))))
    } else {
      setBeanValue(new BigNumber(-1))
      setPodValue(new BigNumber(0))
    }
  }
  useEffect(() => {
    if (section === 1) {
      setBeanValue(props.harvestablePodBalance)
      setPodValue(props.harvestablePodBalance)
    }
  }, [props.harvestablePodBalance, section])

  const handleTabInfoChange = (event, newSectionInfo, newPageZero) => {
    setSectionInfo(newSectionInfo)
    setPage(newPageZero)
  }
  const handlePageChange = (event, newPage) => { setPage(newPage) }

  let claimableFromVal = settings.claim ? props.beanClaimableBalance : 0
  let maxFromVal = section === 0 ? props.beanBalance.plus(claimableFromVal) : props.harvestablePodBalance
  let maxToVal = section === 0 ? props.soil : props.harvestablePodBalance

  if (settings.mode === null) {
    if (props.beanBalance.isGreaterThan(0)) setSettings(p => {return {...p, mode: SwapMode.Bean}})
    else if (props.ethBalance.isGreaterThan(0)) setSettings(p => {return {...p, mode: SwapMode.Ethereum}})
    else if (props.beanBalance.isEqualTo(0) && props.ethBalance.isEqualTo(0)) setSettings(p => {return {...p, mode: SwapMode.Ethereum}})
  }

  function fromValueUpdated(newFromNumber, newEthFromNumber) {
    const newFromValue = MinBNs([newFromNumber, maxFromVal, maxToVal])
    setFromValue(TrimBN(newFromValue, 6))
    if (section === 0) {
      const newFromEthValue = MinBNs([newEthFromNumber, getFromAmount(maxToVal.minus(MaxBN(newFromValue, new BigNumber(0))), props.ethReserve, props.beanReserve, ETH.decimals)])
      BigNumber.set({ DECIMAL_PLACES: 6 })
      const buyBeans = getToAmount(newFromEthValue, props.ethReserve, props.beanReserve)
      BigNumber.set({ DECIMAL_PLACES: 18 })
      setBuyBeanValue(buyBeans)
      setFromEthValue(TrimBN(newFromEthValue, 6))
      const sowedBeans = MaxBN(buyBeans, new BigNumber(0)).plus(MaxBN(newFromValue, new BigNumber(0)))
      setToValue(TrimBN(sowedBeans.multipliedBy((new BigNumber(1)).plus(props.weather.dividedBy(100))),6))
    } else {
      setToValue(TrimBN(newFromValue, 8))
      let harvestedPods = new BigNumber(0)
      let plots = []
      if (newFromNumber.isGreaterThan(0).toString()) {
        Object.keys(props.harvestablePlots).some(key => {
          plots.push(toStringBaseUnitBN(new BigNumber(key), BEAN.decimals))
          harvestedPods = harvestedPods.plus(props.harvestablePlots[key])
          return harvestedPods.isGreaterThanOrEqualTo(newFromNumber)
        })
      }
      setHarvestPlots(plots)
    }
  }

  function handleForm() {
    if (toValue.isLessThanOrEqualTo(0)) return

    if (section === 0) {
      const claimable = settings.claim ? props.claimable : null
      if (fromEthValue.isGreaterThan(0)) {
        const beans = MaxBN(toBaseUnitBN(fromValue,BEAN.decimals), new BigNumber(0)).toString()
        const eth = toStringBaseUnitBN(fromEthValue,ETH.decimals)
        const buyBeans = toStringBaseUnitBN(buyBeanValue.multipliedBy(settings.slippage),BEAN.decimals)
        buyAndSowBeans(beans, buyBeans, eth, claimable, () => {fromValueUpdated(new BigNumber(-1), new BigNumber(-1))})
      } else {
        sowBeans(toStringBaseUnitBN(fromValue,BEAN.decimals), claimable, () => {fromValueUpdated(new BigNumber(-1), new BigNumber(-1))})
      }
    } else {
      harvest(harvestPlots, () => {
        fromValueUpdated(new BigNumber(-1))
      })
    }
  }

  /* Input Fields */

  const beanInputField = (
    section === 1
      ? <TokenInputField
          balance={props.harvestablePodBalance}
          token={fromToken}
          value={fromValue}
        />
      : <InputFieldPlus
          balance={props.beanBalance}
          claim={settings.claim}
          claimableBalance={props.beanClaimableBalance}
          handleChange={(v) => { fromValueUpdated(v, fromEthValue) }}
          token={fromToken}
          value={fromValue}
          visible={settings.mode !== SwapMode.Ethereum}
        />
  )
  const ethInputField = (
    section === 0
      ? <EthInputField
          balance={props.ethBalance}
          buyBeans={buyBeanValue}
          claim={settings.claim}
          claimableBalance={props.claimableEthBalance}
          handleChange={(v) => fromValueUpdated(fromValue, v)}
          mode={settings.mode}
          sellEth={fromEthValue}
          updateExpectedPrice={props.updateExpectedPrice}
          value={fromEthValue}
        />
      : null
  )

  /* Output Fields */

  const toPodsField = (
    <TokenOutputField
      token={toToken}
      value={toValue}
      decimals={BEAN.decimals}
      mint
    />
  )

  if (section > sectionTitles.length-1) setSection(0)

  var sectionTitlesInfo = []
  var sectionsInfo = []
  if (props.plots !== undefined && (Object.keys(props.plots).length > 0 || props.harvestablePodBalance.isGreaterThan(0))) {
    sectionsInfo.push(
      <ListTable
        claimableBalance={props.harvestablePodBalance}
        claimableCrates={props.harvestablePlots}
        crates={props.plots} asset={FarmAsset.Pods}
        description='Sown Plots will show up here'
        handleChange={handlePageChange}
        indexTitle='Place in Line'
        index={parseFloat(props.harvestableIndex)}
        page={page}
        title='Plots'
      />
    )
    sectionTitlesInfo.push('Plots')
  }

  const showListTables = (
    sectionsInfo.length > 0
      ? <div style={{marginTop: '61px'}}>
          <BaseModule
            handleTabChange={handleTabInfoChange}
            section={sectionInfo}
            sectionTitles={sectionTitlesInfo}
            showButton={false}
          >
            {sectionsInfo[sectionInfo]}
          </BaseModule>
        </div>
      : null
  )

  const allowance = (
    (settings.mode === SwapMode.Bean || settings.mode === SwapMode.BeanEthereum)
    && section === 0
      ? props.beanstalkBeanAllowance
      : new BigNumber(1)
  )

  /* Transaction Details, settings and text */

  const sowText = (
    buyBeanValue.plus(fromValue).isEqualTo(props.soil)
      ? `Sowing Maximum Soil With ${displayBN(props.weather)}% Weather`
      : `Sowing With ${displayBN(props.weather)}% Weather`
  )
  const sowTextField = (
    section === 0
      ? <div style={{marginTop: '-5px', fontFamily: 'Futura-PT-Book'}}>{sowText}</div>
      : null
  )
  const noSoilTextField = (
    section === 0 && props.soil.isEqualTo(0)
      ? <div style={{marginTop: '-2px', fontFamily: 'Futura-PT-Book'}}>Currently No Soil</div>
      : null
  )
  const frontrunTextField = (
    settings.mode !== SwapMode.Bean && settings.slippage.isLessThanOrEqualTo(SLIPPAGE_THRESHOLD)
      ? <FrontrunText />
      : null
  )
  const showSettings = (
    section === 0
      ? <SettingsFormModule
          settings={settings}
          setSettings={setSettings}
          handleMode={() => fromValueUpdated(new BigNumber(-1), new BigNumber(-1))}
          hasClaimable={props.hasClaimable}
          hasSlippage={true}
          showUnitModule={section === 0}
        />
      : null
  )
  function transactionDetails() {
    if (toValue.isNegative() || toValue.isZero()) {
      return <></>
    }
    return (
      <>
      <ExpandMoreIcon color='primary' style={{marginBottom: '-14px', width: '100%'}} />
      {toPodsField}
      {sowTextField}
      </>
    )
  }

  return (
    <>
    <BaseModule
      allowance={allowance}
      resetForm={() => { setSettings({...settings, mode: SwapMode.Ethereum}) }}
      handleApprove={approveBeanstalkBean}
      handleForm={handleForm}
      handleTabChange={handleTabChange}
      isDisabled={toValue.isLessThanOrEqualTo(0) && allowance.isGreaterThan(0)}
      section={section}
      sectionTitles={sectionTitles}
      setAllowance={props.setBeanstalkBeanAllowance}
    >
      {beanInputField}
      {ethInputField}
      {noSoilTextField}
      {frontrunTextField}
      {transactionDetails()}
      {showSettings}
    </BaseModule>
    {showListTables}
    </>
  )
}
